<template>
  <div class="app-container">
    <!-- 头部搜索 -->
    <!-- <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="searchInit"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
      </el-form>
    </div> -->
    <el-row>
      <table-com
        :ref="refName"
        :loading="loading"
        :selection="false"
        :table-column-sort="true"
        :table-data="tableData"
        :column-list="columnList"
        :show-operate="false"
        :operation-width="180"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <!-- <template slot-scope="scope">
          <el-button type="warning" icon="el-icon-edit">编辑</el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini">删除</el-button>
        </template> -->
      </table-com>
    </el-row>
    <!-- <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" /> -->
  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import { getRelatedDocumentListApi } from '@/apis/modules/trace'

export default {
  name: 'RelatedDocument',
  components: {
  },
  mixins: [paginationPage],
  data() {
    return {
      refName: 'baseTable',
      loading: false,
      query: {
      },
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'fileName',
          label: '文件名称',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'suffix',
          label: '文件后缀',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'createDate',
          label: '上传时间',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      columnSets: []
    }
  },
  computed: {
  },
  mounted() {
    this.init()
  },
  methods: {
    init(type) {
      this.loading = true
      const params = {
        ...this.query,
        page: {
          ...this.pageInfo
        },
        nameOrCode: this.$route.params.barcode
        // orderId: '141',
        // deliveryReceiptId: '322'
      }
      return new Promise((resolve, reject) => {
        getRelatedDocumentListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            // const { list, ...pageInfo } = data
            // this.initPage(pageInfo)
            this.tableData = data
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
